.header{
  display: flex;
  justify-content: space-between;
  margin: 10px;
  border: 1px solid  black;
}

.nav-items > ul{
  list-style-type: none;
  display: flex;
}

.nav-items > ul > li{
  padding: 10px;
}

.logo{
  width: 100px;
}

.card{
  width: 10rem;
  border: 1px solid black;
  padding: 1rem;
  margin: 1rem;
}

.card > img {
  width: 100%;
}

.resturant-list{
  display: flex;
  flex-wrap: wrap;
}